import React from 'react';
import { useForm } from 'react-hook-form';
import { RouteComponentProps } from 'react-router';
import { Col, Row } from 'reactstrap';
import { EditForm, EditViewProps, getSelectedItem, InputGroupInline, WindowComponent } from 'summer';
import { CLASS_NAME, Ocorrencia } from '../../../../../models/dtos/ger/ocorrencia';

interface OcorrenciaProps extends EditViewProps<Ocorrencia> {}

const OcorrenciaEditView: React.FC<OcorrenciaProps & RouteComponentProps> = props => {
  const selectedItem = getSelectedItem(props);
  const formProps = useForm<Ocorrencia>({ defaultValues: selectedItem, mode: 'onBlur' });

  return (
    <WindowComponent title="Gia - Ocorrência" wikiId={39}>
      <EditForm formProps={formProps} editViewProps={props} dtoClassName={CLASS_NAME}>
        <Row>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { required: true, maxLength: 7 } }}
              id="codigo"
              isPrependLabel={true}
              label="Código"
              placeholder="Informe o Código"
              inputProps={{ autoFocus: true }}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { maxLength: 60 } }}
              id="tipo"
              isPrependLabel={true}
              label="Tipo"
              placeholder="Informe o Tipo"
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { maxLength: 60 } }}
              id="subtipo"
              isPrependLabel={true}
              label="Subtipo"
              placeholder="Informe o Subtipo"
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { required: true, maxLength: 300 } }}
              id="descricao"
              isPrependLabel={true}
              label="Descrição"
              placeholder="Informe a Descrição."
            />
          </Col>
        </Row>
      </EditForm>
    </WindowComponent>
  );
};

export default OcorrenciaEditView;
